import { FC } from 'react';
import { Product } from '../../../types';

interface Props {
	product: Product;
}

const ProductCard: FC<Props> = ({
	// product: {
	// 	name,
	// 	media: { source },
	// 	description,
	// 	price: { formatted },
	// 	conditionals
	// }
	product
}) => {
	const {
		name,
		media: { source },
		price: { formatted }
	} = product;
	return (
		<div className="product popup-catch">
			<h1 className="popup-receive">{name}</h1>
			<div className="product__content">
				<img
					src={source}
					alt={name}
					width="169"
					height="177"
					className="product__img popup-receive"
				/>
				<aside>
					<div className="product__price">{`${formatted} zł`}</div>
					<button type="button" className="btn product__btn btn--green popup popup-receive">
						Do koszyka
					</button>
				</aside>
			</div>
		</div>
	);
};

export default ProductCard;
