import { combineReducers } from '@reduxjs/toolkit';
import { ActionProducts, ActionCart } from './actions';
import { Cart, Product } from '../types';

export interface State {
	products: Product[];
	cart: Cart;
}
const products = (state: Product[] = [], action: ActionProducts) => {
	switch (action.type) {
		case 'products/fetched':
			return action.payload;
		default:
			return state;
	}
};
const cart = (state = [], action: ActionCart) => {
	switch (action.type) {
		case 'cart/fetched':
			return action.payload;
		default:
			return state;
	}
};

export const rootReducer = combineReducers({ products, cart });
