import { FC } from 'react';
import { useSelector } from 'react-redux';
import { Link } from 'react-router-dom';

import { ReactComponent as CartIco } from '../../../assets/cart.svg';
import { State } from '../../../redux/reducers';

import './buttons.css';

const Cart: FC = () => {
	const { total_unique_items: TotalUniqueItems, subtotal } = useSelector(
		(state: State) => state.cart
	);
	return (
		<div className="cart nav__cart popup-catch">
			<Link to="/cart">
				<CartIco className="cart__ico popup-receive" />
				<div className="cart__counter">{TotalUniqueItems}</div>
				<div className="cart__price">{`${subtotal.formatted} zł`}</div>
			</Link>
		</div>
	);
};

export default Cart;
