import { Product } from '../types';

export interface ActionProducts {
	type: 'products/fetched';
	payload: Product[];
}

export const productsFetched = (products: Product[]): ActionProducts => ({
	type: 'products/fetched',
	payload: products
});

export interface ActionCart {
	type: 'cart/fetched';
	payload: Product[];
}
export const cartFetched = (products: Product[]): ActionCart => ({
	type: 'cart/fetched',
	payload: products
});
