import classNames from 'classnames';
import { FC, useState } from 'react';

import './input.css';

interface Props {
	type: string;
	id: string;
	label: string;
	containerClass?: string;
}

const Input: FC<Props> = ({ type, id, label, containerClass }) => {
	const [searchTerm, setSearchTerm] = useState('');
	return (
		<div className={classNames('input-container popup', containerClass, { filled: searchTerm })}>
			<input
				type={type}
				id={id}
				className="input"
				value={searchTerm}
				onChange={(e) => setSearchTerm(e.target.value)}
			/>
			<label htmlFor={id}>{label}</label>
		</div>
	);
};

export default Input;
