import Commerce from '@chec/commerce.js';
import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { cartFetched, productsFetched } from '../redux/actions';
import { Product } from '../types';

export const commerce = new Commerce(process.env.REACT_APP_CHEC_PUBLIC_KEY, true);

export const useCommerceStore = (): void => {
	const dispatch = useDispatch();
	useEffect(() => {
		const fetchData = async () => {
			const { data: productsData = [] }: { data: Product[] } = await commerce.products.list();
			const cartData = await commerce.cart.retrieve();
			console.log(cartData);
			dispatch(productsFetched(productsData));
			dispatch(cartFetched(cartData));
		};
		fetchData();
	}, [dispatch]);
};

export const addToCart = async (productId: number, quantity = 1): Promise<Product[]> => {
	const { cart } = await commerce.cart.add(productId, quantity);
	return cart;
};
export const updateCartQty = async (productId: number, quantity: number): Promise<Product[]> => {
	const { cart } = await commerce.cart.update(productId, { quantity });
	return cart;
};
export const removeFromCart = async (productId: number): Promise<Product[]> => {
	const { cart } = await commerce.cart.update(productId);
	return cart;
};
export const emptyCart = async (): Promise<Product[]> => {
	const { cart } = await commerce.cart.empty();
	return cart;
};
